body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  background: rgb(226, 229, 231);
  color: #14171a;
}

span.lookml {
  font-family: 'Source Code Pro', monospace;
  color:#6997bf;
  font-weight: bold;
}

span.port {
  font-family: 'Source Code Pro', monospace;
  color: rgb(223, 55, 125);
  font-weight: bold;
}

span.special {
  font-family: 'Source Code Pro', monospace;
  color: #000;
  font-weight: bold;
}

.App {
  text-align: center;
}

.App-logo {
  height: 80px;
  padding-bottom: 20px;
}

.App-header {
  background-color: #fff;
  height: 150px;
  padding: 20px;
  color: white;
}

h2 {
  color: #000;
  line-height: 20px;
}
